


































































































































































































































































































































































































































































































































































.content_box {
    display: flex;
    min-height: 500px;
}
.left {
    width: 300px;
    border-right: 1px solid #d8d8d8;
}
.left_title {
    display: flex;
    justify-content: space-between;
    height: 60px;
    line-height: 60px;
    padding: 0 20px;
    font-size: 16px;
    color: #333333;
}
.left_icon {
    font-size: 18px;
    cursor: pointer;
    display: inline-block;
    padding: 6px;
    &:hover {
        opacity: 0.8;
    }
}
.left_icon span {
    padding-left: 10px;
    color: #444444;
}
.left ul li {
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
    // height: 48px;
    // line-height: 48px;
    i {
        font-size: 16px;
    }
}
.left ul li.active {
    background: #fff0ea;
    color: #fb4702;
}
.left ul li:hover {
    background: #fff0ea;
    color: #fb4702;
}
.left ul li span {
    padding: 20px;
}
.right {
    padding: 0px 20px;
    padding-bottom: 84px;
    width: calc(100% - 300px);
}
.right_title {
    height: 60px;
    line-height: 60px;
}
.right_input {
    height: 48px;
    line-height: 48px;
}
.right_table {
    margin-top: 20px;
    border: 1px solid #f5f5f6;
}
